.skills {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 90vh;
}

p{
    font-size: 20px !important;
    max-width: 80%;
    margin-left: auto;
    margin-bottom: 20px;
    margin-right: auto;
}

#heading1 {
    font-size: 70px;
    text-align: center;
    text-shadow: 3px 3px 10px black;
}

ul {
    list-style-type: none;
    line-height: 20px;
}

li {
    display: flex;
    font-size: 30px;
    align-items: center;
    margin: 60px 10px;
}


.skills i {
    font-size: 50px;
}

.languages {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
}

.skills img {
    width: 140px;
}

.ele {
    margin: 30px 60px;
}

.progressbar {
    width: 200px;
    height: 5px;
    margin-top: 10px;
    border: 1px solid #565656;
    border-radius: 5px;
    box-shadow: 0 0 10px rgb(245, 159, 0);
}

.percentage {
    display: block;
    height: 100%;
    background-color: orange;
    border-radius: 5px;
    width: 0;
}

/* Assigning width of each languages*/
.c, .cpp, .python, .html, .css, .js {
    animation: progress 1.5s ease-in 1;

}

.skillbox-animation{
    animation: slide-up 2s ease;
}

.c {
    width: 85%;
}

.cpp {
    width: 80%;
}

.python {
    width: 35%;
}

.html {
    width: 85%;
}

.css {
    width: 75%;
}

.js {
    width: 69%;
}

img{
    mix-blend-mode: multiply;
}

#icon{
    text-decoration: none;
}