
.contact{
    display: flex;
    justify-content: center;
    align-items: center ;
    height: 100vh;
    
}

.container2{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    background-color: rgba(0, 0, 0, 0.484) ;
    padding: 30px;
    width: 60%;
    border-radius: 20px;
    box-shadow: 10px 10px 20px pink;
    opacity: 1;

}

.animate{
    animation-name: slide-in,fadeout;
    animation-duration: 2s;
    animation-delay: 1s 0;
}

.sayhi
{
    display: flex;
    flex-direction: column;
    font-size: 1.4em;
}

.sayhi h4,.sayhi h1{
    color: whitesmoke;
}

.name{
    display: flex;
    margin: 0;
    color: white;
}

input,textarea{
    padding: 10px;
    margin: 10px 10px;
    font-size: 20px;
    border-radius: 5px;
    border: 2px solid transparent;
    outline: none;
    background-color: rgba(0, 0, 0, 0.54);
    color: white;
}

input::placeholder,textarea::placeholder{
    color: rgba(255, 255, 255, 0.482);
}

input[type = "submit"]
{
    align-self: center;
    min-width: 150px;
    width: 30%;
}

input:focus, textarea:focus{
    border: 2px solid rgba(255, 255, 255, 0.699);
}

.form{
    display: flex;
    flex-direction: column;
}

.credits{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    height: 30vh;
    justify-content: center;
    line-height: 30px;
    font-size: 20px;
}

.email,.handles a,.copyright,.lastupdate{
    color: white;
}

a[href = "#container"]{
    text-decoration: none;
    color: #F0E100;
}