.links .active, #link:hover {
    color: #00419E;
    font-weight: 700;
}

nav {
    padding: 5px 120px;
    display: flex;
    justify-content: space-between;
    background-color: #e5e7eb00;
    position: sticky;
    width: 100%;
    transition: background-color 1s ease;
    top: 0px;
    z-index: 5;
}


.logo {
    display: flex;
}


.text {
    width: 80%;
    max-width: 1000px;
}

.name {
    margin: 7px 0 10px 10px;
    color: white;
}

.logo img {
    width: 60px;
}


.logo h1 {
    padding-top: 10px;
}

.links{
    display: flex;
    align-items: center;
}

#link{
    margin-left: 40px;
    font-size: 20px;
    text-decoration: none;
}
