/* animations and responsive styles */


@keyframes progress {
    from {
        width: 0;
    }
  }
  
  @media (max-width:1200px){
    .sayhi{
        font-size: 15px;
    }
    input,textarea{
        font-size: 15px;
    }
  }
  
  @media (max-width:1100px){
    .image{
        width: 200px;
    }
  
    .social i{
        font-size: 25px;
    }
  
    .social a .li{
        font-size: 15px;
        font-weight: 400;
    }
  
    .text{
        font-size: 25px;
    }
  
    .skills img{
        width: 80px;
    }
    .progressbar{
        width: 110px;
        height: 4px;
    }
    #heading1{
        font-size: 30px;
    }
    
  }
  
  @media (max-width:900px){
    .logo h1{
        display: none;
    }
  
    .text{
        word-spacing: 3px;
    }
  
    nav{
        padding: 5px 50px;
    }
  
    .container{
        padding: 0px 50px;
    }
  
    .ele{
        margin: 20px;
    }
  
    input,textarea
    {
        font-size: 12px;
        padding: 5px;
        width: 150px;
    }
  
    .name{
        flex-direction: column;
    }
    .sayhi{
        font-size: 12px;
    }
  }
  
  @media (max-width:600px){
    .logo h1{
        display: none;
    }
  
    nav{
        padding: 0px 0px;
    }
  
    nav a{
        font-size: 15px;
        margin: 10px;
    }
  
    .container{
        padding: 0px 10px;
    }
    .text{
        font-size: 20px;
        word-spacing: 0px;
    }
    .image{
        width: 150px;
    }
    .progressbar{
    box-shadow: 0 0 3px rgb(245, 159, 0);
    }
  
    .container2{
        flex-direction: column;
        align-items: center;
    }
  }
  
  @media (max-width:450px){
    .text{
        font-size: 12px;
    }
  
    .image{
        width: 125px;
    }
  
    nav a{
        margin: 5px;
    }
  
    input,textarea{
        margin: 5px 5px;
        width: 90%;
    }
  
    .container2{
        width: 80%;
    }
  }
  
  @keyframes fadeout{
    from {
        opacity: 0;
    }
    
  }
  
  @keyframes slide-in {
    from {
      transform: translateX(-100%);
    }
    
  }
  
  @keyframes slide-up{
    from{
        transform: translateY(100%);
    }
    to{
        transform: translateY(0%);
    }
  }
  
  @keyframes slide-image{
    from{
        transform: translateX(100%);
    }
  }